import React from 'react'

// IMPORT REACT ROUTER
import {
  Route,
  Link,
  Switch
} from 'react-router-dom'

// IMPORT ICONS
import { 
  IoIosPlayCircle,
  IoIosHeart,
  IoIosStar,
  IoMdCalendar,
} from "react-icons/io";

// IMPORT LAYOUT
import {
  Search,
  Category,
  Details
} from './../export-layout'

// IMPORT COMPONENTS
import {
  Jumbotron,
  SearchBars
} from './../../components'

// IMPORT STYLE LAYOUT
import './layout.css'

// Categories Movie
const categories = [
  { 
    icon: <IoIosPlayCircle size={'1.7em'}/>,
    path: 'now_playing',
    text: 'Now playing'
  },
  { 
    icon: <IoIosHeart size={'1.7em'}/>,
    path: 'popular',
    text: 'Popular'
  },
  { 
    icon: <IoIosStar size={'1.7em'}/>,
    path: 'top_rated',
    text: 'Top rated'
  },
  { 
    icon: <IoMdCalendar size={'1.7em'}/>,
    path: 'upcoming',
    text: 'Upcoming'
  },
]

const HomeCategory = () => {
  return (
    <div className='row'>
      {
        categories.map(({ icon, path, text }) => (
          <div key={text} className='column'>
            <div className='content-cat'>
              {icon}
              <Link 
                to={`/category/${path}`} 
                className='text-cat'>
                {text}
              </Link>
            </div>
          </div>
        ))
      }
    </div>
  )
}

export default function Home() {
  return (
    <>
      <Switch>
        <Route exact path='/'>
          <Jumbotron>
            <h1 className='jumbo-title'>Find Your Favorite Movie</h1>
            <SearchBars />
          </Jumbotron>
          <HomeCategory />
        </Route>
        <Route path='/category/:slug'>
          <Category />
        </Route>
        <Route path='/movie/:id'>
          <Details />
        </Route>
        <Route path='/search/:title'>
          <Search/>
        </Route>
      </Switch>
    </>
  )
}
