import React from 'react'

// IMPORT REACT ROUTER
import {
  NavLink,
} from 'react-router-dom'

// IMPORT ICONS
import { 
  IoIosPlayCircle,
  IoIosHeart,
  IoIosStar,
  IoMdCalendar,
} from "react-icons/io";

// IMPORT CONSTANTS
import { apiCategory } from './..'

// IMPORT STYLE COMPONENT
import './navbar-style.css'

export default function Navbar({ setCategory }) {
  return (
    <nav className='navbar'>
      <ul className='ul-navbar'>
        <NavLink 
          to='/category/now_playing' 
          activeClassName='active'
          className='li-navbar' 
          onClick={() => setCategory(apiCategory('now_playing', 1))}>
          <div><IoIosPlayCircle size={'2.3em'} /></div>
          Now playing
        </NavLink>
        <NavLink 
          to='/category/popular' 
          activeClassName='active'
          className='li-navbar'
          onClick={() => setCategory(apiCategory('popular' , 1))}>
          <div><IoIosHeart size={'2.3em'} /></div>
          Popular
        </NavLink>
        <NavLink 
          to='/category/top_rated' 
          activeClassName='active'
          className='li-navbar'
          onClick={() => setCategory(apiCategory('top_rated' , 1))}>
          <div><IoIosStar size={'2.3em'} /></div>
          Top rated
        </NavLink>
        <NavLink 
          to='/category/upcoming' 
          activeClassName='active'
          className='li-navbar'
          onClick={() => setCategory(apiCategory('upcoming' , 1))}>
          <div><IoMdCalendar size={'2.3em'} /></div>
          Upcoming
        </NavLink>
      </ul>
    </nav>
  )
}
