import React from 'react'

// IMPORT COMPONENTS
import {
  Info,
  Cast
} from './../../components'

// IMPORT STYLE COMPONENT
import './layout.css'

// Method Convert Number To Currency
const toCurrency = number => (
  number.toLocaleString(
    'en-US', 
    { style: 'currency',
      currency: 'USD' }
  )
)

// Method Show Item Array 
const showArrays = arrays => arrays.map(item => ` ${item.name},`)

export default function Information({ content, casts }) {
  // Destructure Objects
  const {
    overview,
    title,
    runtime,
    genres,
    status,
    release_date,
    budget,
    revenue,
    production_companies
  } = content

  // DEBUG
  console.log('Information')

  return (
    <div className='container-details'>
      <Info title='Storyline' content={overview} />
      <Info 
        title='Information' 
        content={
          content 
          ?
            <>
              <p>Title : {title}</p>
              <p>Runtime : {runtime} min</p>
              <p>Genre : {showArrays(genres)}</p>
              <p>Status : {status}</p>
              <p>Release date: {release_date}</p>

              {
                budget === 0 
                ? true
                : 
                  <>
                  <p>Budget: {toCurrency(budget)}</p>
                  <p>Revenue: {toCurrency(revenue)}</p>
                  </>
              }

              <p style={{ textAlign: 'left' }}>Production : {showArrays(production_companies)}</p>
            </>
          : 'Loading...'
        } 
      />
      <Info title='Cast & Crew' content={<Cast contents={casts}/>} />
    </div>
  )
}