import React from 'react'

// IMPORT REACT ROUTER
import {
  Route,
  Link,
  Switch,
  useParams,
} from 'react-router-dom'

// IMPORT ICON
import { RiHome2Line } from 'react-icons/ri'

// IMPORT COMPONENTS
import {
  Navbar,
  Card,
  Pagination,
  Footer
} from './../../components'

// IMPORT CONSTANTS
import { 
  apiCategory,
  useDataApi,
} from './../../constants'

// IMPORT LAYOUT
import {
  Home,
  Details
}  from './../export-layout' 

// IMPORT STYLE LAYOUT
import './layout.css'

export default function Category() {
  let { slug } = useParams()

  const [state, setUrl] = useDataApi(apiCategory(slug, 1), [])
  const { data } = state

  return (
    <Switch>
      <Route exact path='/category/:slug'>
        {/** FIXED HOME */}
        <Link 
          to='/' 
          className='fixed-home-in-category'>
            <RiHome2Line 
              size='2em' 
              color='#fff' 
              className='home-fix-in-category' 
            />
        </Link>

        <div className='container'>
          <div className='navbar-categories'>
            <div className='fixed-navbar'>
              <Navbar setCategory={setUrl}  />
            </div>
          </div>
          <div className='cards-film'>
            {
              data.results ? 
                data.results.map(film => (
                  <Link 
                    key={film.id}
                    to={`/movie/${film.id}`} 
                    className='column-film'>
                    <Card contents={film}/>
                  </Link>
                )) 
              : 'Loading...'
            }
          </div>
          <div className='pagination'>
            <div className='fixed-pagination'>
              <Pagination setPage={setUrl} />
            </div>
          </div>
          <div className='footer-position'>
            <Footer />
          </div>
        </div>
      </Route>

      <Route path='/movie/:id'>
        <Details />
      </Route>
      <Route path='/'>
        <Home />
      </Route>
    </Switch>    
  )
} 

