import React from 'react'

// IMPORT REACT LAZYLOAD
import LazyLoad from 'react-lazyload';

// IMPORT ICON
import { BsPersonFill } from 'react-icons/bs'

// IMPORT CONSTANTS
import { PATH_BASE_IMG } from './..'

// IMPORT STYLE COMPONENT
import './cast-style.css'

export default function Cast({ contents }) {
  return (
    <div className='cast'>
     {
      contents.cast 
      ? 
        contents.cast.map(({ name, character, profile_path }) => (
          <div className='image-cast' key={name}>
            {
              profile_path 
              ? 
                <LazyLoad 
                height={150}
                once>
                <img 
                  src={`${PATH_BASE_IMG}${profile_path}`} 
                  alt={name}
                  width='150px'
                  height='auto'
                />
                </LazyLoad>
              : 
                <BsPersonFill 
                  size='2em' 
                  color='white' 
                  className='no-profile' 
                />
            }
            <div className='character-cast'>{character}</div>
            <div className='name-cast'>{name}</div>
          </div>
        ))
      : 'Loading...'
     }
    </div>
  )
}
