import React, { useReducer } from 'react'

// IMPORT REACT ROUTER
import { Link } from 'react-router-dom'

// IMPORT CONSTANTS
import { themeRate } from './../../constants'

// IMPORT ICONS
import { AiFillStar } from 'react-icons/ai'
import { MdBrokenImage } from 'react-icons/md'

// IMPORT STYLE LAYOUT
import './layout.css'

// Initial State Rate
const initialState = {
  count: 0,
  isClick: true,
}

// Reducer for count rate
const countRateReduce = (state, action) => {
  switch (action.type) {
    case 'up-1':
      return {
        count: action.click ? state.count+1 : state.count-1,
        isClick: !action.click,
      }
    case 'up-2':
      return {
        count: action.click ? state.count-1 : state.count+1,
        isClick: !action.click,
      }
    case 'up-3':
      return {
        count: action.click ? state.count+1 : state.count-1,
        isClick: !action.click,
      }
    case 'up-4':
      return {
        count: action.click ? state.count-1 : state.count+1,
        isClick: !action.click,
      }
    case 'up-5':
      return {
        count: action.click ? state.count+1 : state.count-1,
        isClick: !action.click,
      }
    default: return state
  }
}

// Handle Button Rate: disable and choose theme
const handleButtonRate = ({ type, count, ratekey }, limit = 1) => {
  const storage = Number(localStorage.getItem(`${ratekey}`))
  const isRated = count >= limit || storage >= limit

  switch (type) {
    case 'isDisabled':
      return count > limit 
      || limit < count 
      || storage >= limit

    case 'chooseTheme':
      if (isRated) {
        return 'orange'
      }
      return 'white'
    
    default: return storage
  }
}

// COMPONENT RATE
export default function Rate({ src, title, id }) {
  const [{ count, isClick }, dispatch] = useReducer(countRateReduce, initialState)

  // Name Local Storage
  const rateKey = `rate${id}`

  // PARAMS HANDLE BUTTON RATE DISABLE
  const disable = {
    type: 'isDisabled',
    count,
    ratekey: rateKey
  }
  // PARAMS HANDLE BUTTON RATE THEME
  const theme = {
    type: 'chooseTheme',
    count,
    ratekey: rateKey
  }

  // User Rate Saved
  const userRate = Number(localStorage.getItem(`${rateKey}`))
  
  // Handling Submit User Rate to Local Storage
  const submitRate = (total_rate, ratekey) => {
    // If total rate is 0
    if (total_rate < 1) {
      return console.log('Can\'t send rate user to local storage !')
    }

    localStorage.setItem(`${ratekey}`, `${total_rate}`)
  }
  
  // DEBUG
  console.log('rate')

  return (
    <div className='container-rate'>
      <div className='content-rate'>

        {/** IF IMAGE UNDEFINED, SHOW ICON */}
        {
          src 
          ? 
            <img 
              src={`https://image.tmdb.org/t/p/original/${src}`}
              alt={title}
              width='100%'
              height='auto' 
            />
          : 
            <MdBrokenImage 
              color='white' 
              size='10em' 
              style={{ margin: '0 auto', opacity: '67%' }} 
            />
        }
        
        {/** RATE ICONS */}
        <div className='icon-rate'>
          <button 
            disabled={handleButtonRate(disable, 1)} 
            onClick={() => dispatch({ type: 'up-1', click: isClick })}>
            <AiFillStar 
              className='rate-1' 
              color={handleButtonRate(theme, 1)} 
              size='2em'
            />
          </button>
          <button 
            disabled={handleButtonRate(disable, 2)} 
            onClick={() => dispatch({ type: 'up-2', click: isClick })}>
            <AiFillStar 
              className='rate-2' 
              color={handleButtonRate(theme, 2)} 
              size='2em'
            />
          </button>
          <button 
            disabled={handleButtonRate(disable, 3)} 
            onClick={() => dispatch({ type: 'up-3', click: isClick })}>
            <AiFillStar 
              className='rate-3' 
              color={handleButtonRate(theme, 3)} 
              size='2em'
            />
          </button>
          <button 
            disabled={handleButtonRate(disable, 4)}
            onClick={() => dispatch({ type: 'up-4', click: isClick })}>
            <AiFillStar 
              className='rate-4' 
              color={handleButtonRate(theme, 4)} 
              size='2em'
            />
          </button>
          <button 
            disabled={handleButtonRate(disable, 5)}
            onClick={() => dispatch({ type: 'up-5', click: isClick })}>
            <AiFillStar 
              className='rate-5' 
              color={handleButtonRate(theme, 5)} 
              size='2em'
            />
          </button>
        </div>

        {/** HIDE OR SHOW BUTTON SUBMIT */}
        { 
          userRate > 0 
          ? 
            <p 
              className='desc-rate'>
              Your rating is given
              <i 
                style={{ 
                  color: themeRate(userRate*2),
                  marginLeft: '5px' 
                }}>
                {userRate*2}/10
              </i>
            </p>
          : 
            count > 0 
            ? 
              <Link
                to={`/movie/${id}/detail`} 
                onClick={() => submitRate(count, rateKey)}
                className='rate-submit' 
                type='submit'>
                Submit
              </Link>
            : false
        }
      </div>
    </div>
  )
}