import React from 'react'

// IMPORT REACT ROUTER
import { 
  NavLink, 
  useLocation 
} from 'react-router-dom'

// IMPORT CONSTANTS
import { apiCategory } from './..'

// IMPORT STYLE COMPONENT
import './pagination-style.css'

const numberPages = [
  { page: 2},
  { page: 3},
  { page: 4},
  { page: 5}
]

export default function Pagination({ setPage }) {
  let location = useLocation()
  const params = location.pathname.substr(10)

  return (
    <div className='comp-pagination'>
      {/*<a disabled className='arrow' href='#'>&uarr;</a>*/}
      {
        numberPages.map(({ page }) => (
          <NavLink 
            key={page} 
            to={location.pathname} 
            onClick={() => setPage(apiCategory(params, page))}>
            {page}
          </NavLink>
        ))
      }
      {/*<a disabled className='arrow' href='#'>&darr;</a>*/}
    </div>
  )
}

