import React from 'react'

// IMPORT REACT LAZYLOAD
import LazyLoad from 'react-lazyload';

// IMPORT ICONS
import { MdBrokenImage } from 'react-icons/md'

// IMPORT CONSTANTS
import { PATH_BASE_IMG, themeRate } from './..'

// IMPORT STYLE COMPONENT
import './card-style.css'

export default function Card(
  { contents: 
    { title, 
      poster_path, 
      vote_average, 
      release_date 
    }
  }) {

  return (
    <div className='card'>
      <div className='image'>
        {
          poster_path 
          ?
            <LazyLoad 
              offset={100}
              height={250}
              once>
              <img 
                src={`${PATH_BASE_IMG}${poster_path}`} 
                alt={title}
                width='150px'
                height='250px'
              />
            </LazyLoad>
          : 
            <div className='no-image'>
              <MdBrokenImage 
                color='#B00020' 
                size='8em' 
                style={{ 
                  width: '9.5em',
                  height: '14em' 
                }} 
              />
            </div>
        }
        <div 
          style={{ 
            color: themeRate(vote_average), 
             
          }} 
          className='card-rate'>
          {vote_average}
        </div>
      </div>
      <div className='contents'>
        <div className='card-date'>
          {release_date}
        </div>
        <div className='card-title'>
          <h1>{title}</h1>
        </div>
      </div> 
    </div>
  )
}


