import React from 'react'

// IMPORT COMPONENTS
import {
  Empty,
  Info
} from './../../components'

// IMPORT CONSTANTS
import { isAvailable } from './../../constants'

// IMPORT STYLE LAYOUT
import './layout.css'

const reviewInfo = {
  border: '1px solid #fff',
  borderRadius: '5px',
  flex: '40%',
  maxHeight: '300px'
}

export default function Reviews({ reviews }) {

  // DEBUG
  console.log('reviews')

  return (
    <div className='container-review'>
      {
        isAvailable(reviews.results) 
        ? 
          reviews.results.map(({ author, content }) => (
            <div key={author}>
              <Info 
                style={reviewInfo} 
                title={author} 
                content={content} 
              />
            </div>
          ))
        : 
          <Empty 
            img='https://i.ibb.co/qsGd3qP/empty.png' 
            message='No reviews yet' 
            custom={{ margin: '0 auto' }}
          />
      }
    </div>
  )
}