import React, { useState } from "react";

// IMPORT REACT ROUTER
import {
  Route,
  Link,
  Switch,
  useParams,
} from "react-router-dom";

// IMPORT ICONS
import { FaBars } from "react-icons/fa";

// IMPORT COMPONENTS
import { 
  Card, 
  Footer 
} from "./../../components";

// IMPORT CONSTANTS
import { 
  apiSearch, 
  useDataApi, 
  isAvailable 
} from "./../../constants";

// IMPORT LAYOUT
import { 
  Category, 
  Details 
} from "./../export-layout";

// IMPORT STYLE LAYOUT
import "./layout.css";

export default function Search() {
  let { title } = useParams();

  const [isOpen, setIsOpen] = useState(false);
  const [value, setValue] = useState("");
  const [state, setUrl] = useDataApi(apiSearch(title), []);
  const { data } = state;

  return (
    <Switch>
      <Route path="/search/:title">
        <div className="container-search">
          {/** DROPDOWN NAVBAR */}
          <div className="navbar-search">
            <div className="dropdown">
              <FaBars
                className="open"
                size="2em"
                color="white"
                onClick={() => setIsOpen(!isOpen)}
              />
              {isOpen ? (
                <div className="dropdown-content">
                  <div className="content-search-navbar">
                    <span className="close" onClick={() => setIsOpen(!isOpen)}>
                      &times;
                    </span>
                    <Link to="/category/now_playing">Now playing</Link>
                    <Link to="/category/popular">Popular</Link>
                    <Link to="/category/top_rated">Top rated</Link>
                    <Link to="/category/upcoming">Upcoming</Link>
                  </div>
                </div>
              ) : (
                false
              )}
            </div>

            {/** INPUT SEARCH */}
            <div className="input-search-navbar">
              <input
                type="text"
                onChange={({ target }) => setValue(target.value)}
              />
              <Link
                to={`/search/${value}`}
                onClick={() => setUrl(apiSearch(value))}>
                &rarr;
              </Link>
            </div>
          </div>

          {/** RESULT */}
          <div className="cards-film">
            {isAvailable(data.results) ? (
              data.results.map((film) => (
                <Link
                  key={film.id}
                  to={`/movie/${film.id}`}
                  className="column-film">
                  <Card contents={film} />
                </Link>
              ))
            ) : (
              <div className="movie-not-found">Movie Not Found</div>
            )}
          </div>

          {/** FOOTER */}
          <Footer />
        </div>
      </Route>
      <Route path="/movie/:id">
        <Details />
      </Route>
      <Route path="/category/:slug">
        <Category />
      </Route>
    </Switch>
  );
}
