import React from 'react'

// IMPORT REACT ROUTER
import {
  Route,
  NavLink,
  Switch,
  useParams,
} from 'react-router-dom'

// IMPORT ICON
import { RiHome2Line } from 'react-icons/ri'

// IMPORT COMPONENTS
import {
  Empty,
  Video
} from './../../components'

// IMPORT CONSTANTS
import { 
  apiDetails,
  useDataApi,
  isAvailable
} from './../../constants'

// IMPORT LAYOUT
import {
  Home,
  Search,
  Category
} from './../export-layout'

// IMPORT INTERNAL
import Information from './information'
import Reviews from './reviews'
import Rate from './rate'

// IMPORT STYLE LAYOUT
import './layout.css'

export default function Details() {
  const { id } = useParams()

  // GET DATA FROM API
  const [video] = useDataApi(apiDetails(id, 'videos'),'')
  const [details] = useDataApi(apiDetails(id),'')
  const [cast] = useDataApi(apiDetails(id, 'credits'), [])
  const [review] = useDataApi(apiDetails(id, 'reviews'),[])

  console.log('LayoutDetail')

  return (
    <Switch>
      <Route path='/movie/:id'>
      <div className='container-detail'>
        <div className='detail-video'>
          {
            isAvailable(video.data.results) 
            ? <Video src={video.data.results[0].key} />
            : 
              <Empty 
                img='https://i.ibb.co/SPdJ7nZ/video.png' 
                message='Video not found' 
              />
          }
        </div>
        <div className='detail-information'>
          <NavLink 
            to='/search/random' 
            className='fixed-home'>
            <RiHome2Line 
              size='2em' 
              color='white' 
              className='home-fix' 
            />
          </NavLink>
          <div className='nav-detail'>
            <NavLink 
              to={`/movie/${id}/detail`} 
              activeClassName='nav-active-detail'>
              Details
            </NavLink>
            <NavLink 
              to={`/movie/${id}/reviews`} 
              activeClassName='nav-active-detail'>
              Reviews
            </NavLink>
            <NavLink 
              to={`/movie/${id}/rate`} 
              activeClassName='nav-active-detail'>
              Rate
            </NavLink>
          </div>
          <Switch>
            <Route exact path={`/movie/${id}/detail`}>
              <Information content={details.data} casts={cast.data} />
            </Route>
            <Route path={`/movie/${id}/reviews`}>
              <Reviews reviews={review.data} />
            </Route>
            <Route path={`/movie/${id}/rate`}>
              <Rate 
                src={details.data.poster_path} 
                title={details.data.title}
                id={id}
              />
            </Route>
          </Switch>
        </div>
      </div>
      </Route>
      
      <Route path='/category/:slug'>
        <Category />
      </Route>
      <Route path='/'>
        <Home />
      </Route>
      <Route path='/search/:title'>
        <Search />
      </Route>
    </Switch>
  )
}

